
export default {
    cacherElements() {
        var elements = document.getElementsByClassName("invisible");
        elements.forEach(x => {
            x.style.animation = "disparition 2s forwards";
        })

        elements = document.getElementsByClassName("modal");
        elements.forEach(x => {
            x.style.animation = "disparition 2s forwards";
        })

        elements = document.getElementsByClassName("fondModal");
        elements.forEach(x => {
            x.style.animation = "disparition 2s forwards";
        })
    }
}











